import {Resolver, Arg, Ctx, Query} from "type-graphql";
// import bcrypt from "bcryptjs";
import LectureEntity from "@interface/entity/LectureEntity";
import LectureContentEntity from "@interface/entity/LectureContentEntity";
import ResolverContext from "../type/ResolverContext";
import 'babel-polyfill'


@Resolver()
export class LectureResolver{
	
	@Query(() => LectureEntity)
    async lecuture(
		@Ctx() ctx: ResolverContext, 
		@Arg('id') id:string
	): Promise<LectureEntity | undefined> {
		console.log('타도리 츠이타')
		const repository = ctx.db.getRepository(LectureEntity)
		return repository.findOne(id)
	}

	@Query(() => [LectureEntity])
    async lecutures(
		@Ctx() ctx: ResolverContext
	): Promise<LectureEntity[] | undefined> {
		const repository = ctx.db.getRepository(LectureEntity)
		return repository.find()
	}
	
}

export default LectureResolver